@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Varela Round', sans-serif;
  font-weight: 400;
  color: #ffffff;
  line-height: 1.5;
  background-color: #000000;
  height: 100%;
}

h1 {
  font-family: 'Varela Round', sans-serif;
  font-size: 300%;
  color: #ffffff;
  margin: 0;
}

h2 {
  font-family: 'Varela Round', sans-serif;
  font-size: 140%;
  text-align: center;
  color: #ffffff;
  margin: 0;
}

p {
  font-family: 'Varela Round', sans-serif;
  font-size: 115%;
  color: #b8b8b8;
}

a {
  color: #b8b8b8;
  text-decoration: none;
  transition: 0.25s ease-in-out;
}

a:hover {
  color: #ffffff;
}

main {
  width: 90%;
  max-width: 500px;
  margin: 100px auto 0;
}

input {
  position: relative;
  width: 100%;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  font-size: 18px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  background-color: #3e3e3e;
  border: 0px solid transparent;
}

.links {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;

  margin: 30px auto;
}

.weather,
.headlines {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  min-height: 200px;
  border-radius: 10px;
  background-color: #3e3e3e;
  margin-bottom: 30px;
  padding: 10px 15px;
}

.weather {
  align-items: center;
}

.weather > img {
  width: 50px;
}

.headlines {
  align-items: flex-start;
  flex-wrap: nowrap;
}

.headlines > p {
  font-size: 90%;
  margin: 5px 0;
}

.calendar {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  min-height: 50px;
  border-radius: 10px;
  background-color: #3e3e3e;
  margin-bottom: 30px;
  padding: 10px 15px;
}

.calendar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.calendar li {
  border-left: 2px solid #599afc;
  padding-left: 10px;
  margin-bottom: 10px;
}

.calendar li:last-child {
  margin-bottom: 0px;
}

.calendar p {
  font-size: 90%;
}

.cal-header {
  font-size: 100%;
  font-weight: bold;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

